.text {
    &-gray {
        color: #d6d7e4;
    }
    &-secondary {
        color: #7e8193;
    }
}
.my-stories-search {
    flex: calc(100% - 2.5rem);
    max-width: calc(100% - 2.5rem);
}
input {
    outline: none !important;
    &.checkbox-save {
        visibility: hidden;
        display: none;
        & + label {
            padding-left: 2.5rem;
            position: relative;
            &::before {
                content: "";
                position: absolute;
                left: 0rem;
                top: 1rem;
                background: transparent;
                width: 1.5rem;
                height: 1.5rem;
                border: 1px solid;
                border-radius: 6px;
            }
        }
        &:checked {
            & + label {
                &::before {
                    background: url(../img/checked.svg) no-repeat center;
                    background-size: cover;
                }
            }
        }
    }
}
.editor-container {
    width: 100vw;
    height: 80vh;
    left: 0;
    top: 0;
}
.text-input-container {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(3px);
    background: rgba(black, 0.3);
    z-index: 20;
}
.grid-brush-panel {
    grid-template-columns: 100px auto;
}
.bg {
    &-black-custom {
        background: #242630;
    }
}
.text {
    &-black-custom {
        color: #242630;
    }
}
.bg-blured {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    backdrop-filter: blur(2px);
    z-index: 100;
    background: rgba(black, 0.4);
    overflow: hidden;
}
.no-scroll {
    height: 100vh;
    overflow: hidden;
}
